<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">ADD LISTING</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">OWNER NAME</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.ownerName"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">IC</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.ic"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">CONTACT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="listing.contact"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMAIL</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.email"></fg-input>
            </div>
          </div>
          <hr>
          <div slot="header">
            <h5 class="title">ADD LISTING DETAIL</h5>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">BLOCK NO</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.blockno"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">SQFT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="listing.sqft"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">RENTAL FEE</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.rentalFees"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">MANAGEMENT FEE</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.managementFees"></fg-input>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-4">
              <n-button class="button" type="info" @click.native="post(); handleAdd();">ADD</n-button>
              <!-- <n-button class="button" type="danger">CANCEL</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import { Switch } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [Radio.name]: Radio,
    [Switch.name]: Switch
  },
  data() {
    return {
      listing: {
        ownerName: "",
        ic: "",
        contact: "",
        email: "",
        blockno: "",
        sqft: "",
        rentalFees: "",
        managementFees: "",
        availability: false,
        status: "OCCUPIED"
      },
      switches: {
        defaultOn: true
      }
    };
  },
  methods: {
    post() {
      this.$http
        .post(
          "https://admindashboard-b2ab9.firebaseio.com/listing.json",
          this.listing
        )
    },
    handleAdd() {
      swal({
        title: `You added ${this.listing.ownerName}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill",
        onClose: () =>{
            window.location.href = `#/listing/listing`;
          }
      });
    }
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}
</style>
